<template>
  <div>
      <div class="head">
      <span @click="comeback"><van-icon name="arrow-left" /> </span> <span> </span>
    </div>
       <van-search  v-model="ipt" show-action label="" placeholder="">
  <template #action> </template></van-search> 
         <ul>
             <li @click="goHome(item) " v-for="item in mapList" :key="item.id">
                 {{item.name}}
             </li>
         </ul>

  <p>:</p>
    <div id="container">
 </div> 
  </div>
</template>

<script>
import { mapMutations  } from "vuex";
export default {
    data() {
        return {
            ipt: '',
            cityText:"",
            mapList:[]
        };
    },
    watch:{
         ipt(){
             AMap.plugin("AMap.AutoComplete",()=>{
                 var autoOptions ={
                     city:this.cityText
                 }
                 //AutoComplete
                  var AutoComplete =new AMap.AutoComplete(autoOptions);
                  //
                  AutoComplete.search(this.ipt,(status,result)=>{
                      console.log("",result);
               
                     this.mapList = result.tips;
                  })
             })
         }
    },
    methods:{
        goHome(val){
         this.addcityInfo(val);
         this.$router.push("/home")
        },
      ...mapMutations({
          addcityInfo:"uptCityInfo"
      }),
            comeback(){
    this.$router.push(`/home`)
    },
    },
    computed:{

    },
    mounted(){
     var _this = this;
        AMap.plugin('AMap.CitySearch', function () {
            var citySearch = new AMap.CitySearch()
            citySearch.getLocalCity(function (status, result) {
                if (status === 'complete' && result.info === 'OK') {
                // result
                  
                    _this.cityText = result.city;
                   
                }
            })
        })
        if(this.$store.state.cityInfo.name){
            var cityInfo = this.$store.state.cityInfo;
             var map = new AMap.Map('container',{
                zoom:18,
                center: [cityInfo.location.lng, cityInfo.location.lat]
            });
             var marker = new AMap.Marker({
                position:[cityInfo.location.lng, cityInfo.location.lat]//
            })
            map.add(marker);//
            return
        }
        var map = new AMap.Map('container',{
            zoom:8
        });
}
}
</script>
<style lang="scss" scoped>
@import "./scss/index.scss" ;

</style>